body {
  font-family: 'Montserrat';
  color: $primary-text-color;
}

section {
  max-width: 1280px;
  margin: 0 auto;
  padding-bottom: 60px;

  @media screen and (min-width: $desktop) {
    padding-bottom: 100px;
  }
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;

  @media screen and (min-width: $tablet) {
    padding-left: 24px;
    padding-right: 24px;
    max-width: 768px;
  }

  @media screen and (min-width: $desktop) {
    max-width: 1280px;
  }
}

.title {
  display: flex;
  align-items: center;

  margin-bottom: 32px;
  
  font-size: 24px;
  line-height: 1.2;

  text-transform: uppercase;

  color: $primary-text-color;

  @media screen and (min-width: $tablet) {
    font-size: 44px;
    margin-bottom: 60px;
  }
  @media screen and (min-width: $desktop) {
    margin-bottom: 80px;
  }

  &::after {
    content: '';
    
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    
    margin-left: 14px;

    height: 1px;

    background-color: $primary-text-color;
  }
}


.content__button {
    display: block;
    padding: 14px 28px;
    width: 208px;

    margin-left: auto;
    margin-right: auto;

    background-color: $button-color;
    border-radius: 38px;


    font-weight: 500;
    font-size: 20px;
    line-height: calc(24 / 28);

    color: $primary-white-color;

    &:hover {
        background-color: $button-hover-color;
    }

    &:focus {
        background-color: $button-focus-color;
    }

    @media screen and(min-width: $tablet) {
        padding: 16px 32px;
    }
}

.modal__button {
  display: flex;
  width: 100%;
  justify-content: center;
  padding-bottom: 10px;
  padding-top: 14px;
  font-weight: 600;
  font-size: 18px;
  line-height: calc(22 / 18);
  color: $primary-white-color;
  background-color: $button-color;
  border-radius: 8px;
  @media screen and (min-width: $mobile) and (max-width: $tablet) {
    padding-bottom: 10px;
    padding-top: 10px;
    font-size: 14px;
    line-height: calc(17 / 14);
  }
  @media screen and (min-width: 768px) and (max-width: $desktop) {
  }
  @media screen and (min-width: $desktop) {
  }
}